.cards-screen-header {
  position: fixed;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  height: 85px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
  box-shadow: 0px 3px 4px rgba(0, 0, 0, 0.1); }
  .cards-screen-header .logo-img {
    height: 70px; }
  .cards-screen-header .cards-screen-header-btn {
    position: absolute;
    right: 20px;
    display: flex;
    flex-direction: column;
    align-items: flex-end; }
    .cards-screen-header .cards-screen-header-btn .button-theme {
      height: 40px;
      widows: 96px; }
    .cards-screen-header .cards-screen-header-btn .selection {
      text-align: right;
      font-family: 'GothamNarrow-Book';
      font-size: 14px;
      color: #393939;
      margin: 0px;
      margin-top: 4px; }
  .cards-screen-header .cards-screen-header-back-btn {
    position: absolute;
    left: 20px; }
    .cards-screen-header .cards-screen-header-back-btn .button-theme {
      height: 40px;
      background-color: white;
      border: 2px solid #27B6F2; }
      .cards-screen-header .cards-screen-header-back-btn .button-theme .button-theme-text {
        color: #27B6F2; }
  @media screen and (max-width: 767px) {
    .cards-screen-header .cards-screen-header-btn {
      right: 10px; }
    .cards-screen-header .cards-screen-header-back-btn {
      left: 10px; } }

@media screen and (max-width: 767px) {
  .cards-screen-header {
    height: 70px; } }



