.app {
  padding-top: 115px; }
  .app .navbar {
    margin-bottom: 0px;
    border: none;
    background-color: white; }
  .app .navbar-default {
    height: 115px;
    background-color: white;
    box-shadow: 0px 3px 4px rgba(0, 0, 0, 0.1); }
    .app .navbar-default .navbar-header {
      margin-top: 8px; }
      .app .navbar-default .navbar-header a img {
        height: 70px; }
      .app .navbar-default .navbar-header .navbar-toggle {
        position: absolute;
        top: 14px;
        right: 0;
        border: none; }
    @media screen and (max-width: 767px) {
      .app .navbar-default .navbar-header {
        margin-top: 0px; } }
    .app .navbar-default .navbar-nav {
      margin: 10px; }
    .app .navbar-default .navbar-nav > li {
      width: 160px;
      margin-right: -10px; }
    @media screen and (max-width: 767px) {
      .app .navbar-default .navbar-nav > li {
        width: auto; } }
    .app .navbar-default .navbar-item {
      display: flex;
      justify-content: center;
      height: 55px;
      margin-top: 12px; }
      .app .navbar-default .navbar-item .navbar-item-image {
        position: absolute;
        z-index: -1;
        height: inherit; }
      .app .navbar-default .navbar-item span {
        margin-top: 12px;
        text-decoration: none;
        color: black;
        font-family: 'Handvetica';
        font-size: 18px; }
      .app .navbar-default .navbar-item span:hover {
        color: black; }
    @media screen and (max-width: 1024px) {
      .app .navbar-default .navbar-item {
        height: 55px;
        align-items: center; }
        .app .navbar-default .navbar-item span {
          margin-top: -8px;
          display: flex;
          text-align: center; } }
    @media screen and (max-width: 767px) {
      .app .navbar-default .navbar-item {
        margin-top: 0px; } }
    @media screen and (max-width: 767px) {
      .app .navbar-default .navbar-collapse {
        background-color: white;
        margin-top: 40px; } }
  .app .navbar-fixed-top {
    border: none; }

@media screen and (max-width: 767px) {
  .app {
    padding-top: 90px; }
    .app .navbar-default {
      height: 90px; } }



