.cards-list {
  width: 100%;
  box-sizing: content-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0px 0px 0px -10px; }
  .cards-list .text-view {
    margin: 125px 0px 20px 0px;
    width: 300px; }
  @media screen and (max-width: 767px) {
    .cards-list .text-view {
      margin: 100px 0px 20px 0px; } }
  .cards-list .text-section-h1 {
    color: black; }
  .cards-list .text-body {
    color: black; }
  .cards-list .cards {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center; }
    .cards-list .cards .my-masonry-grid {
      display: -webkit-box;
      /* Not needed if autoprefixing */
      display: -ms-flexbox;
      /* Not needed if autoprefixing */
      display: flex;
      /* gutter size offset */
      width: 800px; }
    @media screen and (max-width: 1280px) {
      .cards-list .cards .my-masonry-grid {
        width: 729px; } }
    @media screen and (max-width: 1100px) {
      .cards-list .cards .my-masonry-grid {
        width: 656px; } }
    .cards-list .cards .my-masonry-grid_column {
      /* gutter size */
      background-clip: padding-box;
      display: flex;
      flex-direction: column;
      align-items: center; }
    .cards-list .cards .my-masonry-grid_column > div {
      /* change div to reference your elements you put in <Masonry> */
      background: grey;
      margin-bottom: 30px; }
    @media screen and (max-width: 992px) {
      .cards-list .cards .cards-items {
        display: flex !important;
        flex-direction: column;
        align-items: center; } }

@media screen and (max-width: 767px) {
  .cards-list {
    padding: 0px 0px !important; } }

.five-card-list {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .five-card-list .align-center {
    display: flex;
    flex-direction: column;
    align-items: center; }
  .five-card-list .text-view {
    margin: 125px 0px 20px 0px;
    width: 300px; }
  @media screen and (max-width: 767px) {
    .five-card-list .text-view {
      margin: 100px 0px 20px 0px; } }
  .five-card-list .text-section-h1 {
    color: black; }
  .five-card-list .text-body {
    color: black; }



