.school {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  height: auto; }
  .school .school-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
    .school .school-content .meee-image {
      margin: 20px 5px;
      width: auto; }
    .school .school-content .school-body {
      margin: 20px 5px;
      display: flex;
      position: relative;
      flex-direction: column;
      align-items: center;
      justify-content: center; }
      .school .school-content .school-body .bg-image {
        width: auto; }
      .school .school-content .school-body .text-view {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-items: center;
        padding: 0px 36px; }
        .school .school-content .school-body .text-view .text-section-h1 {
          font-size: 30px;
          line-height: 30px; }
        .school .school-content .school-body .text-view .text-body {
          font-size: 20px;
          line-height: 20px;
          padding: 0px 12px; }
    .school .school-content .input-btn {
      margin: 20px 5px; }
      .school .school-content .input-btn button {
        height: 50px;
        width: 300px;
        background-color: #009EE2;
        border-radius: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0px 10px;
        font-family: 'GothamNarrow-Book';
        font-size: 18px;
        color: white;
        margin: 0px;
        text-align: center;
        border: none;
        box-shadow: 0px 4px 10px 2px rgba(0, 0, 0, 0.16); }



