.cards-screen-layout .body-center {
  min-height: 100vh;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center; }

@media screen and (max-width: 767px) {
  .cards-screen-layout .body-center {
    height: auto; } }

.cards-screen-layout .no-padding {
  padding-left: 0px;
  padding-right: 0px; }

.cards-screen-layout .no-scrollbar {
  overflow: hidden; }
  .cards-screen-layout .no-scrollbar .scrollbox {
    height: 100vh;
    overflow-y: auto;
    margin-right: -10px; }

.cards-screen-layout .bg-grey {
  background-color: #fafafa; }

.cards-screen-layout .bg-shadow {
  box-shadow: -3px 0px 4px rgba(0, 0, 0, 0.1); }

@media screen and (max-width: 767px) {
  .cards-screen-layout .no-scrollbar {
    padding: 0px !important; } }



